<template>
  <div style="overflow:hidden">
    <div class="containers">
      <img src="../../assets/joinus/bg-join.png" alt="">
    </div>
    <div class="join">
      <div class="container" style="overflow:hidden">
        <v-title :title="this.$t('m.joinUs')"></v-title>
        <div class="accordion" @mouseleave="mouseLeave">
          <div class="img-group" v-for="(item, index) in imgs"
               @mouseenter="mouseEnter(index)" :ref="'accordion' + index">
            <img :src="item.src" alt="">
            <transition name="fade">
              <div class="mask" v-if="index !== active"></div>
            </transition>
          </div>
        </div>
      </div>
    </div>
    <div class="join2">
      <img src="../../assets/joinus/img-join-7.png" alt="">
      <div class="container">
        <div class="title">{{$t('m.joinText')}}
        </div>
      </div>
    </div>
    <v-table></v-table>
  </div>
</template>
<script>
  import VTable from './item/table'
  export default {
    data () {
      return {
        active: 0,
        imgs: [
          {src: require('../../assets/joinus/img-join-1.png')},
          {src: require('../../assets/joinus/img-join-2.png')},
          {src: require('../../assets/joinus/img-join-3.png')},
          {src: require('../../assets/joinus/img-join-4.png')},
          {src: require('../../assets/joinus/img-join-5.png')},
          {src: require('../../assets/joinus/img-join-6.png')}
        ]
      }
    },
    created () {
      window.scrollTo(0, 0)
    },
    methods: {
      mouseEnter (i) {
        this.$refs.accordion1[0].style.transition = 'all 0.8s'
        this.$refs.accordion2[0].style.transition = 'all 0.8s'
        this.$refs.accordion3[0].style.transition = 'all 0.8s'
        this.$refs.accordion4[0].style.transition = 'all 0.8s'
        this.$refs.accordion5[0].style.transition = 'all 0.8s'
        if (i === 0) {
          this.$refs.accordion1[0].style.marginLeft = '0px'
          this.$refs.accordion2[0].style.marginLeft = '-120px'
          this.$refs.accordion3[0].style.marginLeft = '-120px'
          this.$refs.accordion4[0].style.marginLeft = '-120px'
          this.$refs.accordion5[0].style.marginLeft = '-120px'
          this.active = 0
        } else if (i === 1) {
          this.$refs.accordion1[0].style.marginLeft = '-120px'
          this.$refs.accordion2[0].style.marginLeft = '0px'
          this.$refs.accordion3[0].style.marginLeft = '-120px'
          this.$refs.accordion4[0].style.marginLeft = '-120px'
          this.$refs.accordion5[0].style.marginLeft = '-120px'
          this.active = 1
        } else if (i === 2) {
          this.$refs.accordion2[0].style.marginLeft = '-120px'
          this.$refs.accordion3[0].style.marginLeft = '0px'
          this.$refs.accordion4[0].style.marginLeft = '-120px'
          this.$refs.accordion5[0].style.marginLeft = '-120px'
          this.$refs.accordion1[0].style.marginLeft = '-120px'
          this.active = 2
        } else if (i === 3) {
          this.$refs.accordion1[0].style.marginLeft = '-120px'
          this.$refs.accordion2[0].style.marginLeft = '-120px'
          this.$refs.accordion3[0].style.marginLeft = '-120px'
          this.$refs.accordion4[0].style.marginLeft = '0px'
          this.$refs.accordion5[0].style.marginLeft = '-120px'
          this.active = 3
        } else if (i === 4) {
          this.$refs.accordion1[0].style.marginLeft = '-120px'
          this.$refs.accordion2[0].style.marginLeft = '-120px'
          this.$refs.accordion3[0].style.marginLeft = '-120px'
          this.$refs.accordion4[0].style.marginLeft = '-120px'
          this.$refs.accordion5[0].style.marginLeft = '0px'
          this.active = 4
        } else if (i === 5) {
          this.$refs.accordion1[0].style.marginLeft = '-120px'
          this.$refs.accordion2[0].style.marginLeft = '-120px'
          this.$refs.accordion3[0].style.marginLeft = '-120px'
          this.$refs.accordion4[0].style.marginLeft = '-120px'
          this.$refs.accordion5[0].style.marginLeft = '-120px'
          this.active = 5
        }
      },
      mouseLeave () {
        this.active = 0
        this.$refs.accordion1[0].style.marginLeft = '0px'
        this.$refs.accordion2[0].style.marginLeft = '-120px'
        this.$refs.accordion3[0].style.marginLeft = '-120px'
        this.$refs.accordion4[0].style.marginLeft = '-120px'
        this.$refs.accordion5[0].style.marginLeft = '-120px'
      }
    },
    components: {
      VTable
    }
  }
</script>
<style scoped lang="stylus">
  @import '../../common/stylus/mixin.styl'
  &.fade-enter-active, &.fade-leave-active
    transition: all 0.3s
  &.fade-enter, &.fade-leave-to
    transform: translate3d(0, -100%, 0)
  .containers
    width 1200px
    margin 0 auto
    position relative
    height 438px
    img
      position absolute
      left -360px
  .join
    margin-top 64px
    margin-bottom 82px
    .title
      padding-bottom 10px
      text-align center
      border-bottom 3px solid $color
      display inline-block
      width 80px
      font-family: MicrosoftYaHei;
      font-size: 18px;
      font-weight: bold;
      text-align: left;
      color: #541f7e;
    .accordion
      margin-top 40px
      white-space: nowrap;
      .img-group
        position relative
        display inline-block
        overflow hidden
        img
          width 300px
          height 482px
        .mask
          position absolute
          top 0
          height 482px
          width 300px
          background #3c5a9a
          opacity 0.8
        &:nth-child(2)
          margin-left -10px
        &:nth-child(3)
          margin-left -120px
        &:nth-child(4)
          margin-left -120px
        &:nth-child(5)
          margin-left -120px
        &:nth-child(6)
          margin-left -120px
  .join2
    height 420px
    position relative
    img
      width 100%
      height 420px
      position absolute
      top 0
    .container
      position relative
      height 420px
    .title
      position absolute
      z-index 110
      top 50%
      left 50%
      transform translate(-50%, -50%)
      font-family: MicrosoftYaHei;
      font-size: 36px;
      font-weight: bold;
      text-align: center;
      color: #ffffff;
</style>


